//import { useEffect } from 'react';
import { Row, Col } from 'react-bootstrap'
import { useContext, useEffect,useLayoutEffect, useState } from 'react'
import { useParams, useHistory } from "react-router-dom"

import * as BRA from 'lib_bra'
import Video from 'partials/video'
import { RoomsContext } from 'utils/ctx_rooms'
import { StreamContext } from 'utils/ctx_streaming'
import { MediaContext } from 'utils/ctx_mediadevices'
import { AuthContext } from 'utils/ctx_authentication'

export default ({ ...props }) => {
    const history  = useHistory();
    const {roomId} = useParams();
    const auth     = useContext(AuthContext);
    const rooms    = useContext(RoomsContext);
    const media    = useContext(MediaContext);
    const wrtc     = useContext(StreamContext);
    const [selected, setSelected] = useState(null);
    
    useEffect(() => {
        function unload(){
            rooms.leaveRoom();
            document.body.addEventListener('unload', unload);
        }
        document.body.addEventListener('unload', unload);
    return ()=>{
        //unload();
    }},[])

    useLayoutEffect(()=>{
        if(!rooms.ready) return;

        (async ()=>{
            const room = rooms.getRoomInfo(roomId);
            if(!room){
                return history.push('/')
            }
            
            if(![ room.master, ...room.guests].includes(auth.user)){
                await rooms.joinRoom(roomId)
                .catch( ()=> {
                    history.push('/')
                })
            }

            rooms.setCurrent(room);
        })();

    },[rooms.ready, roomId]);

    useLayoutEffect(()=>{
        if(!(auth.isLogged && auth.isConnected) || !rooms.current) return;

        //By this point i can guarantee im in the room where it happens
        wrtc.callHangupAll();
        wrtc.callAllUsers(rooms.current);
    },[auth.isLogged && auth.isConnected, rooms.current]);

    useEffect(()=>{},[wrtc.streams])


    
    
    const dummy = BRA.dummyStream.getStream();

    return <div id="room" className="overflow-hidden m-auto" style={{zIndex:1000}}>
        <Row id="content-row" className="g-1" style={{ height:"100%" }}>
  
            <Col id="carousel-col" xs="auto">
                <div id="carousel" className="d-flex flex-column">
                {  Object.entries({local:media.localStream, ...wrtc.streams, ...{pim: dummy, pam:dummy, pum:dummy, pim2: dummy, pam2:dummy, pum2:dummy, pim3: dummy, pam3:dummy, pum3:dummy}}).map(([callId, stream], k)=>{
                //{  Object.entries({...wrtc.streams,local:media.localStream}).map(([callId, stream], k)=>{
                    return <Video key={callId} id={wrtc.getUserId(callId)} src={stream} onClick={()=>{setSelected(callId)}}/>
                })}
                </div>
            </Col>
            <Col id="main-col">
                <Video className="main-video" id={wrtc.getUserId(selected)} stream={wrtc.streams[selected]??media.localStream} />
            </Col>
        </Row>
        <style global jsx>{`
            @import "src/variables.scss";

            #room{
                height:calc(100vh - 3rem - 3rem);
                
                #main-col {
                    height: 100%;
                    overflow: hidden;

                    .Video
                    {
                        width:100%;
                        height:100%;
                        object-fit: cover;
                    }
                }

                #carousel-col {
                    height: 100%;
                    width:166px;
                    overflow-x: hidden;
                    overflow-y: scroll;

                    #carousel{
                        height:100%;
                    }
                }
            }

            @media only screen and (orientation: landscape) and (max-height: 671px) {           
                #room{
                    height:calc(100vh - 2rem);
                }
            }

            @media only screen and (orientation: portrait){
                #content-row{ flex-direction: column; }
                #carousel-col{
                    overflow-x: scroll;
                    overflow-y: hidden;
                    height: 124px !important;
                    width: 100% !important;
                    mouse-wheel:horizontal;
                    #carousel{
                        flex-direction: row !important;
                        border:none;
                        border-top: 1px solid $secondary;
                        border-bottom: 1px solid $secondary;
                        .Video, .Video video{ 
                            width: 166px; 
                            height:124px; 
                            object-fit: cover;
                        }
                    }
                }
            }

        `}</style>
    </div>;
}

